
function ContactForm({handleClose}) {
    return ( 
        /*Contact popup form */
        <div id="contact">
            <h2 id="popupHeader">Contact Me...</h2>
            <div className="row">
                <div className="colum">
                    <form method="post" name="contactForm" action="form-to-email.php">
                        <label for='name'>Name: </label>
                        <input type="text" id="name" placeholder="First and Last Name..." />

                        <label for='email'>Email: </label>
                        <input type="text" id="email" placeholder="Email.." />

                        <label for='message'>Message Me:</label>
                        <textarea id="message" name="message" placeholder="Type message here..."></textarea>
                    </form>
                </div>
            </div>
            <input type="submit" />
            <a href="/#" onClick={() => handleClose('contact')}>Close</a>
        </div>
    );
    
}
export default ContactForm